import React, { Component } from 'react'
import onboarding1 from '../pics/Onboarding_01.png'

export default class Onboarding_03 extends Component {
    render() {
        return (
            <div className="container" style={{alignContent:"center", textAlign:"center",height:"100vh"}}>
            {/* pic up */}
                <div className="partUp">
                    <img alt="firstPic" width="100%" src={onboarding1} className="pictureUp"></img>
                </div>
                <div style={{width:"276pt", marginTop:"10px"}} className="kituv">
                    <h3>מיקרו-התנדבות</h3>
                    <p>רוצה לעזור בקטנה? אתם בוחרים בוחר למי ומתי</p>
                </div>
                <div style={{minHeight:"20%"}} className="space"></div>
                <div style={{width:"100%"}} className="btns">
                    <button style={{width:"250pt", height:"48pt", backgroundColor:"rgb(80, 210, 194)", fontSize:"16pt",color:"white", borderRadius:"50pt"}} className="signUp">SING IN</button>
                </div>
            </div>
        )
    }
}
