import React, { Component } from "react";
import { ViewPager, Frame, Track, View } from "react-view-pager";
import Onboarding_01 from "./Onboarding_01.js";
import Onboarding_02 from './Onboarding_02'
import Onboarding_03 from './Onboarding_03'


export default class ViewPagerAll extends Component {
  render() {
    const nextAction = () => {
      this.track.next();
    };
    const skipAction = () => {
      this.track.next();
      this.track.next();
    };

    return (
      <div style={{boxSizing:"border-box"}}>
        <ViewPager tag="main">
          <Frame className="frame">
            <Track
              ref={c => (this.track = c)}
              viewsToShow={1}
              // infinite
              className="track"
            >
              <View className="view">
                <Onboarding_01 skipAction={skipAction} nextAction={nextAction} />
              </View>
              <View className="view">
                <Onboarding_02 nextAction={nextAction} />
              </View>
              <View className="view">
                <Onboarding_03 />
              </View>
            </Track>
          </Frame>
        </ViewPager>
      </div>
    );
  }
}
